

.lbl_dts
.icon_info{
	margin: 4px 5px 0 0;
	opacity: 0;
	--opacity-btn_hover : opacity 350ms ease-in;
	transition: var(--opacity-btn_hover);
	cursor: pointer;
	position: relative;
}

.lbl_dts:hover  .icon_info{
	opacity: 1;
}

.lbl_dts:hover  .icon_info .boxInfo{
	opacity: 1;
}

.lbl_dts
span{
	font-weight: 600;
	cursor: pointer;
}


.boxOpenInfo{
	position: absolute;
	max-height: 75px;
	transition: opacity 0.25s ease-in;
	opacity: 0;
   
	overflow-y: auto;
	width: 320px;
	top: -30px;
	left: -5px;
} 

.boxInfo{
	padding: 3px 7px;
	background-color: #fff;
	opacity: 0;
	background: #2f2f2f;
	border: 1px solid var(--color_text_dad);
	border-radius: 8px;
	
}

.boxInfo span{
	font-weight: 500;
	font-size: 14px;
	color: #fff;
}